/* üé¨ Î∞∞Í≤Ω ÏòÅÏÉÅ */
#bg-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
}

/* üî≤ ÏÉÅÎã® Î©îÎâ¥ Î∞î */
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.2);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 60px 0 0;
  gap: 20px;
  z-index: 999;
  backdrop-filter: blur(4px);
}

.top-bar a {
  color: black;
  text-decoration: none;
  font-size: 14px;
  font-weight: bold;
  padding: 6px 12px;
  border-radius: 6px;
  background-color: rgba(255, 255, 255, 0.3);
  transition: background-color 0.2s ease-in-out;
}

.top-bar a:hover {
  background-color: rgba(255, 255, 255, 0.6);
}

/* Ï§ëÏïô ÏΩòÌÖêÏ∏† */
.content {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  position: relative;
  margin-top: 30px;
}

.block {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 40px;
  border-radius: 16px;
  color: white;
  text-align: center;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  width: 400px;
  position: relative;
  z-index: 2;
}
