.header-nav {
  position: fixed;
  top: 15px;
  left: 20px;
  display: flex;
  gap: 10px;
  z-index: 1001;
}

.header-nav button {
  background-color: rgba(255, 255, 255, 0.3);
  border: none;
  border-radius: 6px;
  padding: 6px 10px;
  font-size: 18px;
  cursor: pointer;
}

.header-nav button:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
